import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";

const data = [
  { 
    icon: `${process.env.PUBLIC_URL}/images/offerings1.png`, 
    title: "Curriculum-Aligned Digital Content", 
    points: [
      "The largest K–12 digital content repository in India.",
      "Mapped to CBSE, ICSE & State Boards",
      "Multilingual support and contextual teaching toolsSTEAM, Robotics & Coding",
      "Structured programs for Science, Technology, Engineering, Arts & Math",
      "Hands-on robotics kits",
      "Coding courses in Scratch, Python, and AI foundations",
    ]
  },
  { 
    icon: `${process.env.PUBLIC_URL}/images/offerings2.png`, 
    title: "Immersive Learning Labs", 
    points: [
      "AR/VR-based experiential learning environments",
      "Smart labs for Science, Math, and AI",
      "Enhanced visualization for complex concepts"
    ]
  },
  { 
    icon: `${process.env.PUBLIC_URL}/images/offerings3.png`, 
    title: "Adaptive Assessments & Analytics", 
    points: [
      "Data-driven insights for performance tracking",
      "Customized assessments to suit learner profiles",
      " AI-based tools for personalized remediation"
    ]
  },
    { 
    icon: `${process.env.PUBLIC_URL}/images/offerings4.png`, 
    title: "Smart Classroom Hardware", 
    points: [
      "4K Interactive Flat Panels, Digital Boards & Smart Devices",
      "High-speed content delivery & device integration",
      "Minimal infrastructure dependency for rapid deployment"
    ]
  },
    { 
    icon: `${process.env.PUBLIC_URL}/images/offerings1.png`, 
    title: "Curriculum-Aligned Digital Content", 
    points: [
      "The largest K–12 digital content repository in India.",
      "Mapped to CBSE, ICSE & State Boards",
      "Multilingual support and contextual teaching toolsSTEAM, Robotics & Coding",
      "Structured programs for Science, Technology, Engineering, Arts & Math",
      "Hands-on robotics kits",
      "Coding courses in Scratch, Python, and AI foundations",
    ]
  },
];


const Industryslider = () => {
  return (
    <div className="container-fluid px-5 industry-section bg1 OfferingsSlider" id="ourofferings">
  <h3 className="text-center">Our Offerings</h3>
  <p  className="text-center">We provide an end-to-end digital ecosystem for schools, enabling seamless integration of teaching, learning, and assessment. <br />Our comprehensive portfolio includes:</p>
      <div className="d-flex justify-content-between align-items-center mb-4 position-relative">
      
        <div className="d-flex gap-2 Weformobi  ms-auto">
          <button className="nav-arrow swiper-button-prev-custom">&larr;</button>
          <button className="nav-arrow swiper-button-next-custom">&rarr;</button>
        </div>
      </div>

      <Swiper
        modules={[Navigation]}
        spaceBetween={25}
        slidesPerView={1}
        navigation={{
          nextEl: ".swiper-button-next-custom",
          prevEl: ".swiper-button-prev-custom",
        }}
        breakpoints={{
          576: { slidesPerView: 1 },
          768: { slidesPerView: 3 },
          992: { slidesPerView: 4 },
        }}
      >
                {data.map((item, index) => (
            <SwiperSlide key={index}>
              <div className="industry-card text-center industry-card2">
                <img src={item.icon} alt={item.title} className="mb-3 img-fluid" />
                <h6>{item.title}</h6>
                <ul className="text-start">
                  {item.points.map((point, i) => (
                    <li key={i}>{point}</li>
                  ))}
                </ul>
              </div>
            </SwiperSlide>
          ))}
      </Swiper>
    </div>
  );
};

export default Industryslider;
